<template>
    <menu>
        <router-link to="/">
            <img src="@/assets/logo.png" alt="Resultados da Lotofácil" class="logo">
        </router-link>
        <input type="checkbox" id="check">
        <label for="check" class="nav-btn">
            <span></span>
            <span></span>
            <span></span>
        </label>
        <nav>
            <router-link to="/" class="nav-item" @click="uncheck">Resultados</router-link>
            <a href="./#como-apostar-online" class="nav-item" @click="uncheck">Como apostar online?</a>
            <router-link to="/artigos" class="nav-item" @click="uncheck">Artigos</router-link>
            <router-link to="/faqs" class="nav-item" @click="uncheck">FAQs</router-link>
        </nav>
    </menu>
</template>

<script>
    export default {
        data() {
            return {
                checked: true
            }
        },
        methods: {
            async uncheck () {
                document.getElementById("check").checked = !this.checked
            }
        }
    }
</script>

<style scoped>
#check {
    display: none;
    visibility: hidden;
}
@media (max-width: 1024px) {
    menu {
        width: 100%;
        padding: .5rem 2rem;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin: 0 auto;
    }
    img.logo {
        width: auto;
        height: 70px;
        display: table;
    }
    .nav-btn {
        cursor: pointer;
        width: 30px;
        height: 30px;
        border-radius: 10px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;
    }
    .nav-btn span {
        width: 20px;
        height: 2px;
        background: #262626;
        transition: .3s ease all;
    }

    #check:checked ~ .nav-btn span:nth-child(1) {
        opacity: 0;
        visibility: hidden;
        transform: translateX(-10px);
    }

    #check:checked ~ .nav-btn span:nth-child(2) {
        opacity: 1;
        visibility: inherit;
        transform: translateX(0) rotate(45deg);
    }
    #check:checked ~ .nav-btn span:nth-child(3) {
        opacity: 1;
        visibility: inherit;
        transform: translateX(0) translateY(-7px) rotate(-45deg);
    }

    nav {
        display: flex;
        flex-direction: column;
        gap: 2rem;
        position: fixed;
        left: -100%;
        top: 70px;
        background: #FFF;
        width: 100%;
        height: 100vh;
        z-index: 99999;
        padding: 2rem 3rem;
        transition: .3s ease all;
    }

    #check:checked ~ nav {
        left: 0;
    }

    nav a {
        font-size: 18px;
        font-weight: 500;
        color: #262626;
        opacity: 0;
        transition: .3s ease all;
    }

    #check:checked ~ nav a:nth-child(1) {
        animation: .3s sliceToRight ease forwards;
        animation-delay: .1s;
    }
    #check:checked ~ nav a:nth-child(2) {
        animation: .3s sliceToRight ease forwards;
        animation-delay: .2s;
    }
    #check:checked ~ nav a:nth-child(3) {
        animation: .3s sliceToRight ease forwards;
        animation-delay: .3s;
    }
    #check:checked ~ nav a:nth-child(4) {
        animation: .3s sliceToRight ease forwards;
        animation-delay: .4s;
    }
    @keyframes sliceToRight {
        from {
            opacity: 0;
            transform: translateX(-10px);
        }
        to {
            opacity: 1;
            transform: translateX(0);
        }
    }
}

@media (min-width: 1025px) {
    menu {
        width: 100%;
        max-width: 1440px;
        height: 70px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 1rem 2rem;
        margin: 0 auto;
    }
    .logo {
        width: auto;
        height: 70px;
        display: table;
    }
    .nav-btn {
        display: none;
    }
    nav {
        display: flex;
        gap: 2rem;
        justify-content: flex-end;
        align-items: center;
        height: 70px;
    }
    nav a {
        color: #262626;
        font-weight: 400;
        font-size: 16px;
        transition: .3s ease all;
    }
    nav a:hover {
        color: #9530e7;
    }
}
</style>